<template>
  <div>
    <div class="title mb-2">Đặt lại mật khẩu</div>
    <div class="form-group">
        <label for="exampleInputEmail1">Email</label>
        <input
          v-validate="'required|email'"
          v-model="email"
          name="email"
          type="text"
          data-vv-as="Địa chỉ email"
          :class="['form-control', this.errors.first('email') ? 'is-invalid' : '']">

        <div class="text-danger pt-2">{{ this.errors.first('email') }}</div>
    </div>
    <div class="form-group">
        <label for="exampleInputEmail1">Mã token</label>
        <input
          v-validate="'required'"
          v-model="token"
          name="token"
          type="text"
          data-vv-as="Mã token"
          :class="['form-control', this.errors.first('token') ? 'is-invalid' : '']">

        <div class="text-danger pt-2">{{ this.errors.first('token') }}</div>
    </div>
    <div class="form-group">
        <label for="exampleInputEmail1">Mật khẩu mới</label>
        <input
          v-validate="'required|min:8'"
          v-model="pw"
          name="pw"
          type="password"
          data-vv-as="Mật khẩu mới"
          :class="['form-control', this.errors.first('pw') ? 'is-invalid' : '']">

        <div class="text-danger pt-2">{{ this.errors.first('pw') }}</div>
    </div>
    <div class="form-group">
        <label for="exampleInputEmail1">Nhập lại mật khẩu mới</label>
        <input
          v-validate="'required|min:8'"
          v-model="retype_pw"
          name="retype_pw"
          type="password"
          data-vv-as="Nhập lại mật khẩu mời"
          :class="['form-control', this.errors.first('retype_pw') ? 'is-invalid' : '']">

        <div class="text-danger pt-2">{{ this.errors.first('retype_pw') }}</div>
    </div>
    <div class="d-flex form-group">
      <button class="btn btn-primary ml-auto" @click="resetPw">
        <span class="icon-forget-pw d-inline mr-2"></span>
        Cập nhật mật khẩu mới
      </button>
    </div>

  </div>
</template>

<script>
import request from 'superagent'
import appHelper from '../helper/helper.js'

export default {
  data () {
    return {
      email: '',
      token: '',
      pw: '',
      retype_pw: ''
    }
  },

  methods: {
    resetPw () {
      this.$validator.validate().then(result => {
        if (result) {
          if (this.pw !== this.retype_pw) {
            this.errors.add('pw', 'Mật khẩu không bằng mật khẩu nhập lại mật khẩu')
            return
          }

          // Kiểm nhập lại mật khẩu
          request.post('http://deltavn.net/api/password/reset').send({
            token: this.token,
            password: this.pw,
            password_confirmation: this.retype_pw,
            email: this.email
          }).then((res) => {
            if (res.body.error) {
              appHelper.taoModalBaoLoi(res.body.error, this, 'Cập nhật lại mật khẩu thất bại')
              return
            }
            appHelper.taoModalThongBao('Đặt lại mật khẩu thành công', this)
            this.$router.replace('/reset_pw')
          })
        }
      })
    }
  }
}
</script>

<style>

</style>



