<template>
  <div class="w-60">
     <div class="title mb-2">Thông tin phòng khám</div>
           <div v-if="isViewInfoClinic && clinicInfo">
              <p class="mb-1"><b>Tên phòng khám:</b> {{clinicInfo.name}}</p>
              <p class="mb-1"><b>Địa chỉ:</b> {{clinicInfo.address}}</p>
              <p class="mb-1"><b>Mô tả:</b> <span v-html="clinicInfo.description||'Không có'"></span></p>
            </div>

          <hr>
          <div class="title mb-2">Lịch khám bệnh</div>
          <datepicker
            :disabledDates="disableDatePickerDates"
            :language="vi"
            @selected="getShift"
            v-model="timePicker"
            :inline="true"
            :full-month-name="true"
            wrapper-class="w-100 mb-2"
            calendar-class="w-100"
            :monday-first="false"
            format="dd/MM/yyyy"
            :bootstrap-styling="true">
          </datepicker>
          <button class="btn btn-primary m-2" v-for="shift in clinicShifts" :key="shift.id" @click="book(shift)">
            <b>id: {{shift.id}}</b>
            <b>Bắt đầu:</b> {{formatDate(shift.start_shift)}}<br>
            <b>Kết thúc</b> {{formatDate(shift.end_shift)}}<br>
          </button>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import request from 'superagent'
import moment from 'moment'
import Datepicker from 'vuejs-datepicker'
import appHelper from '../../helper/helper.js'
import {vi} from 'vuejs-datepicker/dist/locale'

export default {
  computed: mapState([
    'idToken',
    'user'
  ]),

  methods: {
    book (shift) {
      appHelper.datCauHoi('Bạn có muốn đăng kí ca khám bệnh này không', () => {
        let self = this
        request.post(`http://deltavn.net/api/user/${self.user.id}/book`).set({
          'Authorization': `Bearer ${this.idToken}`
        }).send({
          'shifts_id': [shift.id]
        }).then(() => {
          appHelper.taoModalThongBao('Đăng ký ca thành công. Nhân viên của chúng tôi sẽ liên hệ và xác nhận lại.', this)
        }).catch((err) => {
          appHelper.taoModalBaoLoi('Bạn đã đăng ký ca khám bệnh này rồi', this)
        })
      }, this)
    },

    formatDate (date) {
      return moment(date).format('hh [giờ]: mm [phút]')
    },

    getShift (dateTime) {
      // Load lịch của ngày được chọn lên
      let dateTimeMoment = moment(dateTime)

      // Gửi dữ liệu lấy ca dựa trên thời gian
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: true
      })

      request.get(`http://deltavn.net/api/clinic/${this.$route.params.id}/shift/filter?date=${dateTimeMoment.format('YYYY-MM-DD')}`)
        .set({
          'Authorization': `Bearer ${this.idToken}`
        })
        .then((res) => {
          this.clinicShifts = res.body
        }).finally(() => {
          this.$store.dispatch('set', {
            propertyName: 'isLoading',
            payload: false
          })
        })
    }
  },

  components: {
    Datepicker
  },

  data () {
    return {
      clinicInfo: null,
      clinicShifts: [],
      isViewInfoClinic: true,
      timePicker: new Date(),
      disableDatePickerDates: {
        // Chỉ được chọn hôm nay trở đi
        to: moment().subtract(1, 'day').toDate()
      },
      vi
    }
  },

  created () {
    //

    this.$store.dispatch('set', {
      propertyName: 'isLoading',
      payload: true
    })

    let promises = []

    promises.push(new Promise((resolve) => {
      request.get(`http://deltavn.net/api/clinic/${this.$route.params.id}`)
        .then((res) => {
          this.clinicInfo = res.body.data
          resolve()
        })
    }))

    // Lấy thông tin phòng khám

    // Lấy ca khám bệnh của ngày hôm nay
    promises.push(new Promise((resolve) => {
      let today = moment().format('YYYY-MM-DD')
      request.get(`http://deltavn.net/api/clinic/${this.$route.params.id}/shift/filter`)
        .set({'Authorization': `Bearer ${this.idToken}`})
        .query({date: today})
        .then((res) => {
          this.clinicShifts = res.body
          resolve()
        })
    }))

    // Tất loading khi tải xong dữ liệu
    Promise.all(promises).then(() => {
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: false
      })
    })
  }
}
</script>

<style>
.tab-body {
  padding: 1em;
  border: 1px solid #dee2e6;
  border-top: 0px;
}
</style>



