export default {
  taoModalThongBao (NoiDung, _this) {
    _this.$modal.show('dialog', {
      title: 'Thông báo',
      text: NoiDung,
      buttons: [
        {
          title: 'Đóng',
          default: true,
          handler: () => {
            _this.$modal.hide('dialog')
          }
        }
      ]
    })
  },

  taoModalBaoLoi (NoiDung, _this, tit) {
    _this.$modal.show('dialog', {
      title: tit || 'Đã xảy ra lỗi',
      text: NoiDung,
      buttons: [
        {
          title: 'Đóng',
          default: true,
          handler: () => {
            _this.$modal.hide('dialog')
          }
        }
      ]
    })
  },

  datCauHoi (cauHoi, traLoiCo, _this) {
    _this.$modal.show('dialog', {
      title: 'Xác nhận',
      text: cauHoi,
      buttons: [
        {
          title: 'Có',
          default: true,
          handler: () => {
            _this.$modal.hide('dialog')
            traLoiCo()
          }
        }, {
          title: 'Không',
          default: true,
          handler: () => {
            _this.$modal.hide('dialog')
          }
        }
      ]
    })
  }
}



