<template>
<div>
  <div class="d-flex my-4">
    <button class="btn btn-primary ml-auto" @click="addAvatar">
      <span class="icon-add d-inline mr-2"></span>
      Thêm bác sĩ
    </button>
  </div>

  <div
      :key="doctor.name"
      v-for="(doctor, index) in doctors">
    <hr>
    <div class="form-group">
      <label for="exampleInputPassword1">Học vị :</label>
      <select v-model="doctor.title" class="form-control">
        <option
          :value="index"
          :key="title"
          v-for="(title, index) in titles">
          {{title}}
        </option>
      </select>
    </div>
    <div class="form-group">
      <label for="exampleInputPassword1">Họ tên :</label>
      <input
        v-model="doctor.name"
        :class="['form-control', getError('name' + index) ? 'is-invalid' : '']"
        :name = "'name' + index"
        v-validate="'required'"
        data-vv-as="Họ tên"
        type="text"
        class="form-control"
        placeholder="Họ tên">
      <div class="text-danger pt-2">{{ getError('name' + index) }}</div>
    </div>
    <div class="form-group">
      <label for="exampleInputPassword1">Mô tả :</label>
      <textarea class="form-control" v-model="doctor.description">
      </textarea>
    </div>
    <div class="form-group">
      <label for="exampleInputPassword1">Hình ảnh :</label>
      <input
        @change="(e)=>{setAvatar(doctor, e)}"
        class="form-control"
        type="file"
        id="avatar" name="avatar"
        accept="image/png, image/jpeg" />
      <img :src="doctor.srcImage" >
    </div>
    <div class="d-flex">
      <button class="btn btn-danger ml-auto" @click="deleteAvatar(index)">
        <span class="icon-add d-inline mr-2"></span>
        Xóa bác sĩ
      </button>
    </div>
  </div>

  <hr>
  <div class="form-group d-flex">
    <button class="btn btn-secondary ml-auto mr-3" @click="backCallBack" v-if="isShowBack">
      <span class="icon-undo2 d-inline mr-1"></span>
      Quay lại
    </button>
    <button class="btn btn-primary" @click="onSumbit">
      <span class="icon-edit d-inline mr-1"></span>
      {{sumbitText}}
    </button>
  </div>
</div>
</template>

<script>
import Vue from 'vue'

export default {
  data () {
    return {
      titles: [
        'Tiến sĩ',
        'Thạc sĩ',
        'Phó giáo sư',
        'Giáo sư'
      ],
      data: Object.assign({
        doctors: []
      }, this.initData)
    }
  },
  methods: {
    setAvatar (doctor, e) {
      doctor.image = e.target.files[0]
      let reader = new FileReader()
      reader.onloadend = () => {
        Vue.set(doctor, 'srcImage', reader.result)
      }
      reader.readAsDataURL(doctor.image)
    },

    onSumbit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.submitCallBack(this.data)
        }
      })
    },

    addAvatar () {
      this.doctors.push({
        name: '',
        description: '',
        title: 0,
        image: null
      })
    },

    getError (name) {
      return this.errors.first(name)
    },

    deleteAvatar (index) {
      this.doctors.splice(index, 1)
    }
  },

  computed: {
    doctors () {
      return this.data.doctors.data ? this.data.doctors.data : this.data.doctors
    }
  },

  watch: {
    initData (newVal) {
      this.data = newVal
    }
  },

  props: ['sumbitText', 'submitCallBack', 'initData', 'isShowBack', 'backCallBack']
}
</script>

<style>

</style>



