import Vue from 'vue'

export default {
  TOGGLE (state, propertyName) {
    state[propertyName] = !state[propertyName]
  },

  SET (state, data) {
    state[data.propertyName] = data.payload
  },

  ADD_REPORT (state, report) {
    state.trafficReports.push(report)
  },

  EDIT_REPORT (state, data) {
    // let clonedTrafficReport = JSON.parse(JSON.stringify(state.trafficReports))
    // clonedTrafficReport[data.index] = data.report
    // state.trafficReport = clonedTrafficReport
    Vue.set(state.trafficReports, data.index, data.report)
    // state.trafficReports[data.index] = data.report
  },

  DELETE_REPORT (state, index) {
    state.trafficReports.splice(index, 1)
  }
}



