<template>
  <div>
    <div class="mb-2 title">Quản lý ca khám bệnh</div>
    <div class="mt-2">
      <div class="mb-2 title">Chọn ngày :</div>
      <div class="form-group">
        <datepicker
        :language="vi"
        @selected="getShift"
        v-model="timeFilter"
        :inline="true"
        :full-month-name="true"
        wrapper-class="w-100 mb-2"
        calendar-class="w-100"
        :monday-first="false"
        format="dd/MM/yyyy"
        :bootstrap-styling="true">
        </datepicker>
      </div>
        <hr>
        <div class="mb-2 mt-3 title">Ca khám bệnh :</div>
        <div class="instructuion">
          Chú thích: <br>
          <span class="confirmed-instruction px-1">1</span> Ca khám bệnh này <b>đã xác nhận cho bệnh nhân</b> <br>
          <span class="unconfirmed-instruction px-1">N</span> Ca khám bệnh này <b>chưa xác nhận cho bệnh nhân nào</b> và có <b>N bệnh nhân đang đợi xác nhận</b>
        </div>
        <div class="d-flex p-0 time flex-wrap">
          <div
            :style="{backgroundColor: shiftPicker.backgroundColor, flexBasis}"
            :class="[
              'position-relative mt-5 text-left d-flex align-items-center', shiftPicker.backgroundColor !== defaultColor ? 'no-border' : '', index === begin ? 'begin' : '']"
            v-for="(shiftPicker, index) in shiftPickers" :key="index"
            @mouseover="mouseover(index)"
            @mouseout="mouseup(index)"
            @click="click(shiftPicker, index)">
              <span
                v-if="shiftPicker.title"
                v-html="shiftPicker.title"
                :class="['position-absolute noselect', shiftPicker.minuteTitle ? 'minute-title' : 'hour-title']">

              </span>
              <span class="confirmed" v-if="shiftPicker.confirmed">
                1
              </span>
              <span class="unconfirmed" v-else-if="shiftPicker.unconfirmed">
                {{shiftPicker.unconfirmed}}
              </span>
              <span
                v-if="shiftPicker.text"
                class='text-white font-weight-bold position-absolute noselect w-200 z-9999 text-title'>
                  <popper trigger="click" :options="{placement: 'top'}" v-if="shiftPicker.parrent">
                    <div class="popper">
                      Bắt&nbsp;đầu:&nbsp;{{shiftPicker.parrent.popperStartStr}} <br>
                      Kết&nbsp;thúc:&nbsp;{{shiftPicker.parrent.popperEndStr}} <br>
                      <button class="btn btn-primary btn-sm col my-2" @click="detailShift(shiftPicker.parrent.id)">
                        <span class="icon-info d-inline mr-1"></span>
                        Chi tiết</button>
                      <button class="btn btn-danger btn-sm col" @click="deleteShift(shiftPicker.parrent)">
                        <span class="icon-trash d-inline mr-1"></span>
                        Xóa</button>
                    </div>

                    <span slot="reference">
                      {{shiftPicker.text}}
                    </span>
                  </popper>
                  <span v-else>{{shiftPicker.text}}</span>
              </span>

              <popper trigger="click" :options="{placement: 'top'}" v-if="shiftPicker.parrent">
                <div class="popper">
                  Bắt&nbsp;đầu:&nbsp;{{shiftPicker.parrent.popperStartStr}} <br>
                  Kết&nbsp;thúc:&nbsp;{{shiftPicker.parrent.popperEndStr}} <br>
                  <button class="btn btn-primary btn-sm col my-2">
                    <span class="icon-info d-inline mr-1"></span>
                    Chi tiết</button>
                  <button class="btn btn-danger btn-sm col" @click="deleteShift(shiftPicker.parrent)">
                    <span class="icon-trash d-inline mr-1"></span>
                    Xóa</button>
                </div>

                <div slot="reference">
                  &nbsp;
                </div>
              </popper>
              <span class="noselect" v-else>&nbsp;</span>

            </div>
        </div>
    </div>
    <div class="d-flex form-group mt-3 flex-wrap">
      <button class="btn btn-danger ml-auto mr-2 col-12 col-md-auto mt-xs-3" @click="navigateToCopyShift">
        <span class="icon-copy d-inline mr-1"></span>
        Sao chép ca
      </button>
      <button class="btn btn-secondary col-12 col-md-auto mt-xs-3" @click="()=>{$router.replace('/clinic/administrate')}">
        <span class="icon-undo2 d-inline mr-1"></span>
        Quay lại
      </button>
    </div>
  </div>
</template>

<script>
/**
 * TODO
 * Thêm phút chỗ tiếng
 * Chỉnh chọn ngày thành tiếng việt
 * Copy ca cho ngày
 */
import moment from 'moment'
import Vue from 'vue'
import Datepicker from 'vuejs-datepicker'
import request from 'superagent'
import {mapState} from 'vuex'
import {vi} from 'vuejs-datepicker/dist/locale'
import Popper from 'vue-popperjs'
import 'vue-popperjs/dist/css/vue-popper.css'
import leftPad from 'left-pad'
import appHelper from '../../helper/helper.js'

export default {
  methods: {
    detailShift (id) {
      this.$router.replace(`/clinic/${this.$route.params.id}/shift/${id}/administrate`)
    },

    deleteShift (shift) {
      appHelper.datCauHoi(`Bạn có muốn xóa ca từ ${shift.popperStartStr} đến ${shift.popperEndStr}`, () => {
        request.delete(`http://deltavn.net/api/clinic/${this.$route.params.id}/shift/${shift.id}`)
          .set({
            'Authorization': `Bearer ${this.idToken}`
          }).then(res => {
            // Rerender shift picker
            for (let i = shift.startIndex; i < shift.endIndex + 1; i++) {
              this.shiftPickers[i].parrent = null
              this.shiftPickers[i].backgroundColor = this.defaultColor
              this.shiftPickers[i].text = ''
            }

            // Sếp lại thứ tự text

            // Bỏ đối tượng shift
            let index = this.shifts.findIndex(_shift => _shift.id === shift.id)
            this.shifts.splice(index, 1)

            appHelper.taoModalThongBao('Xóa ca thành công', this)
          })
      }, this)
    },

    mouseover (index) {
      if (this.begin !== null && this.begin !== index) {
        let start = Math.min(this.begin, index)
        let end = start === this.begin ? index : this.begin

        for (let i = start; i <= end; i++) {
          // Không đụng vào phần tử bị locked và begin
          if (!this.shiftPickers[i].parrent && i !== this.begin) {
            this.shiftPickers[i].backgroundColor = this.colors[this.colorIndex]
          }
        }
      }
    },

    mouseup (index) {
      if (this.begin !== null && this.begin !== index) {
        let start = Math.min(this.begin, index)
        let end = start === this.begin ? index : this.begin

        for (let i = start; i <= end; i++) {
          if (!this.shiftPickers[i].parrent && i !== this.begin) {
            this.shiftPickers[i].backgroundColor = this.defaultColor
          }
        }
      }
    },

    click (e, index) {
      // Kiểm tra ngày
      let todayTime = (new Date())
      let filterTime = this.timeFilter
      todayTime.setHours(0, 0, 0, 0)
      filterTime.setHours(0, 0, 0, 0)
      if (todayTime.getTime() > filterTime.getTime()) {
        appHelper.taoModalBaoLoi('Bạn không được tạo ca trong ngày thuộc quá khứ', this)
        return
      }

      // Kiểm tra xem đã chọn chưa
      if (e.parrent != null) {
        // Hiện popper

        return
      }

      // Kiểm tra về sau xem có begin nào đằng sau không. Có -> đây là end
      if (this.begin !== null) {
        // Kiểm tra là bắt đầu unbegin
        if (this.begin === index) {
          this.begin = null
          e.backgroundColor = this.defaultColor
          return
        }

        // Kiểm tra x
        let start = Math.min(this.begin, index)
        let end = start === this.begin ? index : this.begin

        // Check for error here

        // Hỏi xem người dùng có click nhầm hay không :
        let startTimeInMinutes = start === 0 ? 0 : start * this.numTimePerShiftPickerInMinutes
        let endTimeInMinutes = end === 0 ? 0 : (end + 1) * this.numTimePerShiftPickerInMinutes

        let startAt = this.convertMinsToHrsMins(startTimeInMinutes)
        let endAt = this.convertMinsToHrsMins(endTimeInMinutes)
        let date = `ngày ${this.timeFilter.getDate()} tháng ${this.timeFilter.getMonth()} năm ${this.timeFilter.getFullYear()}`

        appHelper.datCauHoi(`Bạn có muốn tạo ca vào ${date} từ ${startAt} đến ${endAt}`, () => {
          for (let i = start; i <= end; i++) {
            if (this.shiftPickers[i].parrent) {
              // Set vị trí hiện tại trong trường hợp di chuyển lên
              this.shiftPickers[end].backgroundColor = this.defaultColor

              // Set từ start lên i
              for (let j = start; j < i; j++) { // đảo ngược từ đâu
                this.shiftPickers[j].backgroundColor = this.defaultColor
              }

              // Tắt sự kiện mouse up:
              this.mouseup(index)
              this.begin = null
              appHelper.taoModalBaoLoi('Đã có ca được tạo trong khoảng thời gian mà bạn chọn rồi', this)
              return
            }

            this.shiftPickers[i].backgroundColor = this.colors[this.colorIndex]
          }

          // Lưu lại ca đã chọn
          // Tìm vị trí, để sắp theo thứ tự
          let newShift = {
            startElem: this.shiftPickers[start],
            startIndex: start,
            endIndex: end,
            popperStartStr: startAt,
            popperEndStr: endAt
          }
          for (let iii = start; iii <= end; iii++) {
            Vue.set(this.shiftPickers, iii, Object.assign(this.shiftPickers[iii], {
              parrent: newShift
            }))
          }

          let isInsertInside = false
          for (let ii = 0; ii < this.shifts.length; ii++) {
            if (this.shifts[ii].startIndex > end) {
              this.shifts.splice(ii, 0, newShift)

              // Gán text : thứ tự ca
              for (let jj = ii; jj < this.shifts.length; jj++) {
                Vue.set(this.shifts[jj].startElem, 'text', `Ca ${jj + 1}`)
              }

              isInsertInside = true
            }

            if (isInsertInside) break
          }

          // Phải chèn ở phỉa ngoài cùng
          if (!isInsertInside) {
            let shiftsNewLength = this.shifts.push(newShift)
            this.shiftPickers[start].text = `Ca ${shiftsNewLength}`
          }

          // format start at and end at
          let startAtTimeObject = new Date(this.timeFilter.toDateString() + ' ' + startAt)
          let endAtTimeObject = new Date(this.timeFilter.toDateString() + ' ' + endAt)
          let startAtStr = this.yyyy_mm_dd_hh_mm_ss(startAtTimeObject)
          let endAtStr = this.yyyy_mm_dd_hh_mm_ss(endAtTimeObject)

          request.post(`http://deltavn.net/api/clinic/${this.$route.params.id}/shift`)
            .set({
              'Authorization': `Bearer ${this.idToken}`
            })
            .send({
              start_shift: startAtStr,
              end_shift: endAtStr
            }).then(res => {
              newShift.id = res.body.data.id
              appHelper.taoModalThongBao('Tạo ca thành công', this)
            })

          // Đổi màu
          this.colorIndex++
          if (this.colorIndex === this.colors.length) {
            this.colorIndex = 0
          }

          this.begin = null
        }, this)
      } else {
        // đây là begin
        this.begin = index
        e.backgroundColor = this.colors[this.colorIndex]
      }
    },

    renderShiftPopper () {

    },

    renderShifts (shifts) {
      // Chỉnh màu, vue event chỉnh riêng, tốn thếm parrent reference
    },

    getShift (dateTime) {
      this.begin = null
      let dateTimeMoment = moment(dateTime)

      // Gửi dữ liệu lấy ca dựa trên thời gian
      let self = this
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: true
      })
      request.get(`http://deltavn.net/api/clinic/${this.$route.params.id}/shift/filter?date=${dateTimeMoment.format('YYYY-MM-DD')}`)
        .set({
          'Authorization': `Bearer ${this.idToken}`
        })
        .then((res) => {
          // Gán shift
          /* eslint-disable camelcase */
          let shifts = res.body

          // Sắp lại ca theo thời gian
          shifts.sort((a, b) => {
            let a_startshift = new Date(a.start_shift).getTime()
            let b_startshift = new Date(b.start_shift).getTime()
            return a_startshift - b_startshift
          })

          // this.shifts.forEach((shift) => {
          //   for (let i = shift.startIndex; i <= shift.endIndex; i++) {
          //     this.shiftPickers[i].backgroundColor = this.defaultColor
          //     this.shiftPickers[i].parrent = null
          //   }

          //   this.shiftPickers[shift.startIndex].text = ''
          // })
          this.createNewShiftPickers()

          // Lọc lại những cái active 1
          shifts = shifts.filter(shift => shift.active === 1)

          // Tạo lại shiftpickers

          shifts.forEach((shift, index) => {
            let start_shift = new Date(shift.start_shift)
            let end_shift = new Date(shift.end_shift)
            let startTimeInMinutes = start_shift.getHours() * 60 + start_shift.getMinutes()
            let startIndex = (startTimeInMinutes / this.numTimePerShiftPickerInMinutes)
            let endTimeInMinutes = end_shift.getHours() * 60 + end_shift.getMinutes()
            let endIndex = (endTimeInMinutes / this.numTimePerShiftPickerInMinutes)

            // Gán shift index
            shift.startIndex = startIndex
            shift.endIndex = endIndex

            for (let i = startIndex; i < endIndex; i++) {
              self.shiftPickers[i].backgroundColor = self.colors[self.colorIndex]
              self.shiftPickers[i].parrent = shift
            }
            self.colorIndex++
            if (self.colorIndex >= self.colors.length) {
              self.colorIndex = 0
            }

            // Format lại thời gian hiện thi trên popper
            shift.popperStartStr = `${leftPad(start_shift.getHours(), 2, 0)}:${leftPad(start_shift.getMinutes(), 2, 0)}`
            shift.popperEndStr = `${leftPad(end_shift.getHours(), 2, 0)}:${leftPad(end_shift.getMinutes(), 2, 0)}`
            shift.startElem = this.shiftPickers[startIndex]
            shift.startElem.text = `Ca ${index + 1}`
            // Lấy thông tin ca

            // Tạo bandage ở phần tử cuối cùng
            // this.shiftPickers[endIndex-1].confirmed = true
            request.get(`http://deltavn.net/api/shift/${shift.id}/user`).set({
              'Authorization': `Bearer ${this.idToken}`
            }).then((res) => {
              let users = res.body.data

              // Check if confirmed
              let confirmedUsers = users.filter(user => user.pivot.confirmed)
              if (confirmedUsers.length === 1) {
                Vue.set(this.shiftPickers[endIndex - 1], 'confirmed', true)
              } else {
                Vue.set(this.shiftPickers[endIndex - 1], 'unconfirmed', users.length)
              }

              // Nothing happend here
            })
          })

          self.shifts = shifts
        }).finally(() => {
          this.$store.dispatch('set', {
            propertyName: 'isLoading',
            payload: false
          })
        })
    },

    yyyy_mm_dd_00_00_00 (now) {
      let year = '' + now.getFullYear()
      let month = '' + (now.getMonth() + 1)
      if (month.length === 1) { month = '0' + month }
      let day = '' + now.getDate()
      if (day.length === 1) { day = '0' + day }
      return year + '-' + month + '-' + day + ' 00:00:00'
    },

    yyyy_mm_dd_hh_mm_ss (now) {
      let year = '' + now.getFullYear()
      let month = '' + (now.getMonth() + 1)
      if (month.length === 1) { month = '0' + month }
      let day = '' + now.getDate()
      if (day.length === 1) { day = '0' + day }
      let hour = '' + now.getHours()
      if (hour.length === 1) { hour = '0' + hour }
      let minute = '' + now.getMinutes()
      if (minute.length === 1) { minute = '0' + minute }
      let second = '' + now.getSeconds()
      if (second.length === 1) { second = '0' + second }
      return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second
    },

    convertMinsToHrsMins: function (minutes) {
      var h = Math.floor(minutes / 60)
      var m = minutes % 60
      h = h < 10 ? '0' + h : h
      m = m < 10 ? '0' + m : m
      return h + ':' + m
    },

    navigateToCopyShift () {
      this.$router.replace(
        `/clinic/${this.$route.params.id}/shift/copy/${this.timeFilter.getTime()}`)
    }
  },

  computed: {
    flexBasis () {
      // Calculate flex basis :
      let numShiftPickersPerRowInHour = (this.numHourPerRow * 60) / this.numTimePerShiftPickerInMinutes
      return 100 / numShiftPickersPerRowInHour + '%'
    },
    ...mapState(['idToken'])
  },

  mounted () {
    this.vi._days = [
      'CN',
      'Th2',
      'Th3',
      'Th4',
      'Th5',
      'Th6',
      'Th7'
    ]
    this.createNewShiftPickers = () => {
      // Render shifttime picker
      let shiftPickers = []
      let numShiftPickers = this.numTimePerDayInMinutes / this.numTimePerShiftPickerInMinutes
      for (let i = 0; i < numShiftPickers; i++) {
        shiftPickers.push({
          backgroundColor: this.defaultColor
        })

        let thisShiftPickerTimeInHour = ((i + 1) * this.numTimePerShiftPickerInMinutes) / 60
        if (Number.isInteger(thisShiftPickerTimeInHour)) {
          shiftPickers[i].title = `${thisShiftPickerTimeInHour}`
        } else {
          // Chuyển sang phút
          let minute = ((i + 1) * this.numTimePerShiftPickerInMinutes)
          if (minute > 60) {
            minute = minute % 60
          }
          shiftPickers[i].minuteTitle = true
          shiftPickers[i].title = `${minute}`
        }
      }
      this.shiftPickers = shiftPickers
    }

    this.createNewShiftPickers()

    // Tạo event resize responsive
    window.onresize = () => {
      let fullWidth = window.innerWidth
      if (fullWidth > 1024) {
        this.numHourPerRow = 4
      } else if (fullWidth < 1024 && fullWidth > 768) {
        this.numHourPerRow = 3
      } else if (fullWidth < 768 && fullWidth > 480) {
        this.numHourPerRow = 2
      } else {
        this.numHourPerRow = 1
      }
    }
    window.onresize()

    // Lấy thông tin ca của ngày hôm nay
    this.getShift(new Date())
  },

  data () {
    return {
      numHourPerRow: 4,
      numTimePerDayInMinutes: 1440, // ngày có 12 tiếng 1440 phút
      numTimePerShiftPickerInMinutes: 5,
      defaultColor: 'white',
      vi: vi,
      begin: null,
      colorIndex: 0,
      colors: ['#EF5350', '#EC407A', '#9C27B0', '#1A237E', '#536DFE', '#2196F3', '#00C853', '#f1c40f', '#F57F17'],
      shifts: [],
      shiftPickers: [],
      timeFilter: new Date()
    }
  },

  components: {
    Datepicker,
    Popper,
    vi
  }
}
</script>

<style>
.confirmed-instruction {
  background: #07df0f;
  color: white;
}

.unconfirmed-instruction {
  background: black;
  color: white;
}

.confirmed {
  color: white;
  background: #07df0f;
  position: absolute;
  top: 0px;
  right: 0;
  height: 100%;
  padding: 1px 7px;
}

.unconfirmed {
  color: white;
  background: black;
  position: absolute;
  top: 0px;
  right: 0;
  height: 100%;
  padding: 1px 7px;
}

.begin {
  border: 2px solid white
}

.minute-title {
  font-size: 11px;
  top: -27px;
  color: #6e6e6e;
  font-weight: bold
}
@media (max-width: 640px) {
  #datepicker-desktop {
      display: none
  }
}

.text-title {
  font-size: 12px
}

@media (max-width: 320px) {
  .text-title {
    letter-spacing: -2px;
  }
}

@media (min-width: 640px) {
  #datepicker-mobile {
      background-color: lightblue;
  }
}

.display {
  letter-spacing: 1px;
  padding-bottom: 5px;
}

.clinic-container {
  border: 1px solid #a5a5a5;
  border-radius: 1px;
}

.btn-grid {
  position: relative;
}

.btn-grid > .badge {
  position: absolute;
  top: -5px;
  right: -5px;
  padding: 5px
}

.time > div {
  cursor: pointer;
}

.time > div:not([class*="no-border"]) {
  border: 1px solid #999;
}

.rightMost {
  right: 0;
}

.hour-title {
  top: -30px;
  letter-spacing: -1px;
  font-weight: bold
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.w-200 {
  width: 200%
}

.z-9999 {
  z-index: 1
}

.popper {
  padding: 5px !important
}
</style>



