<template>
  <div>
    <doctors
        :initData = "doctors"
        sumbitText="Chỉnh sửa bác sĩ"
        :backCallBack="()=>{$router.replace('/clinic/administrate')}"
        :isShowBack="true"
        :submitCallBack="editDoctors" />
  </div>
</template>

<script>
import {mapState} from 'vuex'
import request from 'superagent'
import Doctors from '../../components/Doctors'
import appHelper from '../../helper/helper.js'
export default {
  computed: mapState(['idToken']),
  methods: {
    editDoctors (data) {
      let doctors = data.doctors.data.map(doctor => {
        if (!doctor.image) {
          doctor.image = null
        }

        return doctor
      })

      data.doctors = doctors

      console.log(JSON.stringify(data))

      let pushData = new FormData()
      // Chuyển thành form-data
      for (let key in data) {
        pushData.append(key, data[key])
      }

      fetch('http://deltavn.net/api/clinic/' + this.$route.params.id, {
        method: 'put',
        headers: {
          'Content-Type': 'multipart/form-data',
          'Authorization': `Bearer ${this.idToken}`
        },
        body: pushData
      })
        .then((res) => {
          appHelper.taoModalThongBao('Thay đổi thông tin phòng khám thành công')
          this.$store.dispatch('toggle', 'isShowModal')
        }).finally(() => {
          this.props.hideLoading()
        })
    }
  },

  components: {
    Doctors
  },

  data () {
    return {
      doctors: {}
    }
  },

  created () {
    this.$store.dispatch('set', {
      propertyName: 'isLoading',
      payload: true
    })
    let self = this
    request.get('http://deltavn.net/api/clinic/' + this.$route.params.id).set({
      'Authorization': `Bearer ${this.idToken}`
    }).then(res => {
      self.doctors = res.body.data
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: false
      })
    })
  }
}
</script>

<style>

</style>



