<template>
  <div>
    <div class="title mb-2">Quên mật khẩu</div>
    <div v-if="isMailSend" class="alert alert-success">
      Gửi mail đặt lại mật khẩu thành công. Vui lòng kiểm tra địa chỉ email của bạn để thay đổi mật khẩu
    </div>
    <div v-else>
      <div class="form-group">
        <label for="exampleInputEmail1">Địa chỉ Email</label>
        <input
          v-validate="'required|email'"
          v-model="email"
          name="mail"
          type="email"
          data-vv-as="Địa chỉ email"
          :class="['form-control', this.errors.first('mail') ? 'is-invalid' : '']">
        <small id="emailHelp" class="form-text text-muted">Chúng tôi sẽ gửi mã vào email của bạn. Bạn có thể dùng mã này để đặt lại mật khẩu tại <a href="">đây</a> </small>
        <div class="text-danger pt-2">{{ this.errors.first('mail') }}</div>
      </div>
      <div class="d-flex form-group">
        <button class="btn btn-primary ml-auto" @click="resetPw">
          <span class="icon-send d-inline mr-2"></span>
          Gửi email
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import request from 'superagent'
import appHelper from '../helper/helper.js'

export default {
  data () {
    return {
      email: '',
      isMailSend: false
    }
  },

  methods: {
    resetPw () {
      this.$validator.validate().then(result => {
        if (result) {
          request.post('http://deltavn.net/api/password/forgot').send({
            email: this.email
          }).then((res) => {
            appHelper.taoModalThongBao('Chúng tôi sẽ gửi mã vào email của bạn. Bạn có thể dùng mã này để đặt lại mật khẩu', this)
            this.$router.replace('/reset_pw')
          }).catch(err => {
            appHelper.taoModalBaoLoi(err.response.body.error, this)
          })
        }
      })
    }
  }
}
</script>

<style>

</style>



