<template>
    <div class="">
      <div class="title mb-2">Tìm phòng khám :</div>
      <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputEmail4">Loại</label>
            <select class="form-control" v-model="selectedClinicType">
              <option :key="clinicType.id" v-for="clinicType in clinicTypes" :value="clinicType.id">{{clinicType.name}}</option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="inputPassword4">tên</label>
            <input type="text" class="form-control" placeholder="tên" v-model="selectedClinicName">
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputEmail4">Quận</label>
            <select class="form-control" id="exampleFormControlSelect1" v-model="selectedDistrict" @change="onDistrictChange">
              <option :key="district.id" v-for="district in districts" :value="district.id">{{district.name}}</option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="inputPassword4">Huyện</label>
            <select class="form-control" id="exampleFormControlSelect1" v-model="selectedWard">
              <option :key="ward.id" v-for="ward in wards" :value="ward.id">{{ward.name}}</option>
            </select>
          </div>
        </div>
        <div class="form-group d-flex flex-wrap mt-2">
          <button class="btn btn-secondary mr-2 ml-auto col-12 col-md-auto" @click="autoInputLocation">
            <span class="icon-location d-inline-block mr-1"></span>
            Nhập vị trí tự động
          </button>
          <button class="btn btn-primary col-12 col-md-auto mt-xs-3" @click="onClinicSearch">
            <span class="icon-search d-inline-block mr-2"></span>
            Tìm phòng khám
          </button>
        </div>
    </div>
</template>

<script>
import request from 'superagent'
import appHelper from '../../helper/helper'
import Vue from 'vue'
import {mapState} from 'vuex'

export default {
  computed: mapState([
    'clinics',
    'selectedClinicTypeGlobal',
    'selectedDistrictGlobal',
    'selectedWardGlobal',
    'selectedClinicNameGlobal'
  ]),
  methods: {
    onClinicSearch () {
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: true
      })
      request.get('http://deltavn.net/api/clinic')
        .query({name: this.selectedClinicName})
        .query({district_id: this.selectedDistrict})
        .query({ward_id: this.selectedWard})
        .query({type: this.selectedClinicType})
        .then((res) => {
          this.$store.dispatch('set', {
            propertyName: 'clinics',
            payload: res.body.data
          })

          this.$store.dispatch('set', {
            propertyName: 'isShowModal',
            payload: false
          })

          // Lưu lại cài đặt
          this.$store.dispatch('set', {
            propertyName: 'selectedClinicTypeGlobal',
            payload: this.selectedClinicType
          })

          this.$store.dispatch('set', {
            propertyName: 'selectedDistrictGlobal',
            payload: this.selectedDistrict
          })

          this.$store.dispatch('set', {
            propertyName: 'selectedWardGlobal',
            payload: this.selectedWard
          })

          this.$store.dispatch('set', {
            propertyName: 'selectedClinicNameGlobal',
            payload: this.selectedClinicName
          })
        })
        .finally(() => {
          this.$store.dispatch('set', {
            propertyName: 'isLoading',
            payload: false
          })
        })
    },

    onDistrictChange () {
      var context = this
      Vue.nextTick(function () {
        request.get(`http://deltavn.net/api/district/${context.selectedDistrict}/ward`).then(res => {
          context.wards = res.body.data
          context.selectedWard = context.wards[0].id
        })
      })
    },

    autoInputLocation () {
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: true
      })
      navigator.geolocation.getCurrentPosition((pos) => {
        // Đưa điểm điểm này lên google map để tìm kiếm quận huyện
        request.get('https://maps.googleapis.com/maps/api/geocode/json')
          .query({key: 'AIzaSyA6jVBqVLTXFpNsxmEKx8HTFEIwmiq0usQ'})
          .query({latlng: `${pos.coords.latitude}, ${pos.coords.longitude}`})
          .then((res) => {
            let geocode = res.body.results.filter(result => {
              return result.types.indexOf('street_address') !== -1
            })[0]

            // Find ward and district
            let ward = geocode.address_components.filter(addresscomponent => {
              return addresscomponent.types.indexOf('administrative_area_level_3') !== -1
            })[0].long_name

            let district = geocode.address_components.filter(addresscomponent => {
              return addresscomponent.types.indexOf('administrative_area_level_2') !== -1
            })[0].long_name

            /// Auto complet quận
            let districtFound = this.districts.find(districtToCompare => districtToCompare.name.indexOf(district) !== -1)
            if (districtFound) {
              if (this.selectedDistrict === districtFound.id) {
                // Quận autocomplete đã tìm thấy, không cần load quận, huyện mới nữa
                // Autocomplete phường
                let wardFound = this.wards.find(wardToCompare => wardToCompare.name.indexOf(ward) !== -1)
                if (wardFound) {
                  this.selectedWard = wardFound.id
                }
                return
              }

              this.selectedDistrict = districtFound.id
              request.get(`http://deltavn.net/api/district/${districtFound.id}/ward`).then(res => {
                this.wards = res.body.data

                // Auto complete phường
                let wardFound = this.wards.find(wardToCompare => wardToCompare.name === ward)
                if (wardFound) {
                  this.selectedWard = wardFound.id
                } else {
                  this.selectedWard = this.wards[0].id
                }
              })
            }
          }).finally(() => {
            this.$store.dispatch('set', {
              propertyName: 'isLoading',
              payload: false
            })
          })
      }, () => {
        this.$store.dispatch('set', {
          propertyName: 'isLoading',
          payload: false
        })
        appHelper.taoModalBaoLoi('Không thể truy cập địa chỉ GPS của bạn. Vui lòng xem lại cài đặt hoặc cấp quyền', this)
        return false
      })
    }
  },

  data () {
    return {
      selectedClinicType: 1,
      clinicTypes: [],
      districts: [],
      selectedDistrict: 1,
      wards: [],
      selectedWard: 1,
      selectedClinicName: ''
    }
  },

  created () {
    let promises = []
    this.$store.dispatch('set', {
      propertyName: 'isLoading',
      payload: true
    })

    if (this.selectedClinicNameGlobal) {
      this.selectedClinicName = this.selectedClinicNameGlobal
    }

    // Load loại phòng khám
    promises.push(new Promise((resolve) => {
      request.get('http://deltavn.net/api/clinic-type').then((res) => {
        this.clinicTypes = res.body.data

        // Load loại phòng khám trong cài đặt đã lưu
        if (this.selectedClinicTypeGlobal) {
          this.selectedClinicType = this.selectedClinicTypeGlobal
        } else {
          this.selectedClinicType = this.clinicTypes[0].id
        }

        resolve()
      })
    }))

    // Load cài đặt cũ

    // Load quận
    promises.push(new Promise((resolve) => {
      request.get('http://deltavn.net/api/district').then(res => {
        this.districts = res.body.data

        // load lại quận trong cài đặt
        if (this.selectedDistrictGlobal) {
          this.selectedDistrict = this.selectedDistrictGlobal
        } else {
          this.selectedDistrict = res.body.data[0].id
        }
        request.get(`http://deltavn.net/api/district/${this.selectedDistrict}/ward`).then(res => {
          this.wards = res.body.data

          // load lại phường trong cài đặt
          if (this.selectedWardGlobal) {
            this.selectedWard = this.selectedWardGlobal
          } else {
            this.selectedWard = res.body.data[0].id
          }
          resolve()
        })
      })
    }))

    Promise.all(promises).then(() => {
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: false
      })
    })
  }
}
</script>

<style>
</style>



