<template>
  <div class="w-500">
    <div class="mb-2 title">Đổi thông tin phòng khám :</div>
    <clinicInfo
        :initData = "clinic"
        sumbitText="Chỉnh sửa phòng khám"
        :backCallBack="()=>{$router.replace('/clinic/administrate')}"
        :isShowBack="true"
        :sumbitCallBack="editClinic" />
  </div>
</template>

<script>
import clinicInfo from '../../components/Clinic_Info'
import {mapState} from 'vuex'
import request from 'superagent'
import appHelper from '../../helper/helper.js'

export default {
  computed: mapState(['idToken']),
  created () {
    this.$store.dispatch('set', {
      propertyName: 'isLoading',
      payload: true
    })

    let self = this
    request.get('http://deltavn.net/api/clinic/' + this.$route.params.id).set({
      'Authorization': `Bearer ${this.idToken}`
    }).then(res => {
      self.clinic = res.body.data
    }).finally(() => {
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: false
      })
    })
  },

  data () {
    return {
      clinic: null
    }
  },

  methods: {
    editClinic (data) {
      let doctors = data.doctors.data.map(doctor => {
        if (!doctor.image) {
          doctor.image = null
        } else {
          // format theo dạng hình
        }

        return doctor
      })

      data.doctors = doctors

      request.put('http://deltavn.net/api/clinic/' + this.$route.params.id).send(data).set({
        'Authorization': `Bearer ${this.idToken}`
      }).then(() => {
        appHelper.taoModalThongBao('Thay đổi thông tin phòng khám thành công', this)
        this.$router.replace('/clinic/administrate')
      })
    }
  },

  components: {
    clinicInfo
  }
}
</script>

<style>

</style>



