<template>
  <div v-if="shift">
    <div class="title mb-1">Thông tin ca</div>
    <b>Thời gian bắt đầu:</b> {{shift.start_shift}} <br>
    <b>Thời gian kết thúc:</b> {{shift.end_shift}}
    <table class="table mt-4">
      <thead>
        <tr>
          <th scope="col">Tên</th>
          <th scope="col">Địa chỉ</th>
          <th scope="col">Email</th>
          <th scope="col">Điện thoại</th>
          <th scope="col">Trạng thái</th>
          <th>Hành động</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in users" :key="user.id">
          <th scope="row">{{user.name}}</th>
          <td>{{user.address}}</td>
          <td>{{user.email}}</td>
          <td>{{user.phone}}</td>
          <td>{{user.pivot.confirmed ? 'Đã xác nhận' : 'Chưa xác nhận'}}</td>
          <td>
            <button v-if="user.pivot.confirmed" class="btn btn-link py-0" @click="disapprove(user.id,user)">
              <span class="icon-disapprove d-inline mr-2"></span>
              Hủy xác nhận
            </button>
            <button v-else class="btn btn-link py-0" @click="approve(user.id,user)">
              <span class="icon-approve d-inline mr-2"></span>
              Xác nhận
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="d-flex form-group mt-3 flex-wrap">
      <button class="btn btn-secondary col-12 col-md-auto ml-auto" @click="()=>{$router.replace(`/clinic/${this.$route.params.clinic_id}/shift`)}">
        <span class="icon-undo2 d-inline mr-1"></span>
        Quay lại
      </button>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import request from 'superagent'
import {mapState} from 'vuex'
import appHelper from '../../helper/helper.js'

export default {
  methods: {
    approve (userId, user) {
      appHelper.datCauHoi('Bạn có muốn xác nhận đặt ca này không ?', () => {
        request.put(`http://deltavn.net/api/shift/${this.$route.params.shift_id}/user/${userId}/confirm`).set({
          'Authorization': `Bearer ${this.idToken}`
        }).then((res) => {
          appHelper.taoModalThongBao('Xác nhận đặt ca thành công', this)
          user.pivot.confirmed = 1
        })
      }, this)
    },

    disapprove (userId, user) {
      appHelper.datCauHoi('Bạn có muốn hủy xác nhận đặt ca này không ?', () => {
        request.put(`http://deltavn.net/api/shift/${this.$route.params.shift_id}/user/${userId}/unconfirm`).set({
          'Authorization': `Bearer ${this.idToken}`
        }).then((res) => {
          appHelper.taoModalThongBao('Hủy xác nhận đặt ca thành công', this)
          user.pivot.confirmed = 0
        })
      }, this)
    }
  },

  computed: mapState([
    'idToken',
    'user'
  ]),

  data () {
    return {
      shift: null,
      users: []
    }
  },

  created () {
    this.$store.dispatch('set', {
      propertyName: 'isLoading',
      payload: true
    })

    request.get(`http://deltavn.net/api/clinic/${this.$route.params.clinic_id}/shift/${this.$route.params.shift_id}`).set({
      'Authorization': `Bearer ${this.idToken}`
    }).then((res) => {
      let shift = res.body.data
      shift.start_shift = moment(shift.start_shift, 'YYYY-MM-DD').format('dddd DD MMMM [năm] YYYY [lúc] HH:mm')
      shift.end_shift = moment(shift.end_shift, 'YYYY-MM-DD').format('dddd DD MMM [năm] YYYY [lúc] HH:mm')
      this.shift = shift
    }).finally(() => {
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: false
      })
    })

    request.get(`http://deltavn.net/api/shift/${this.$route.params.shift_id}/user`).set({
      'Authorization': `Bearer ${this.idToken}`
    }).then((res) => {
      this.users = res.body.data
    })
  }
}
</script>

<style>

</style>



