<template>
  <div>
    <!-- <p>
      <label for="exampleInputEmail1"><b>id</b> {{user.id}} : </label> <br>
      <small>Sử dụng id này để mời bạn bè vào nhóm trong ứng dụng điện thoại</small>
    </p> -->
    <div class="title mb-2">Thông tin tài khoản</div>
    <p v-if="user">
      <b>Họ tên</b>: {{user ? user.name : ''}} <br>
      <b>Địa chỉ</b>: {{user.address}} <br>
      <b>Số điện thoại</b>: {{user.phone}} <br>
      <b>Quyền</b>: {{ user.role ? user.role.title : 'Người dùng bình thường' }}
    </p>
    <div class="form-group d-flex flex-wrap mt-2">
      <button @click="$router.replace('/change_password')" class="btn btn-primary mr-2  mr-2 ml-auto col-12 col-md-auto">
        <span class="icon-key d-inline mr-2"></span>
        Đổi mật khẩu</button>
      <button @click="$router.replace('/change_info')" class="btn btn-primary  col-12 col-md-auto mt-xs-3">
        <span class="icon-clipboard-edit d-inline mr-2"></span>
        Chỉnh sửa thông tin</button>
    </div>
  </div>
</template>

<script>
import request from 'superagent'
import { mapState } from 'vuex'
export default {
  methods: {
  },

  data () {
    return {
      user: null
    }
  },

  created () {
    this.$store.dispatch('set', {
      propertyName: 'isLoading',
      payload: true
    })
    request.post('http://deltavn.net/api/me').set({
      'Authorization': `Bearer ${this.idToken}`
    }).then((res) => {
      this.user = res.body.data
    }).finally(() => {
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: false
      })
    })
  },

  computed: mapState([
    'idToken'
  ])
}
</script>

<style>

</style>



