<template>
  <div class="w-60vw">
    <modal name="weeekPickerModal" id="weeekPickerModal">
      <datepicker
        :disabledDates="disableDatePickerDates"
        :language="vi"
        @selected="weekPickerOnSelected"
        :inline="true"
        :full-month-name="true"
        wrapper-class="w-100 mb-2"
        calendar-class="w-100"
        :monday-first="false"
        format="dd/MM/yyyy"
        :bootstrap-styling="true">
        </datepicker>
        <div class="d-flex my-3 mr-2">
          <button class="btn btn-secondary ml-auto mr-2" @click="$modal.hide('weeekPickerModal')">
            <span class="icon-undo2 d-inline-block mr-1"></span>
            Quay lại
          </button>
        </div>
    </modal>
    <modal name="rangePickerError">
      <div class="d-flex my-3 mr-2">
        <button class="btn btn-secondary ml-auto mr-2" @click="$modal.hide('datePickerModal')">
          <span class="icon-undo2 d-inline-block mr-1"></span>
          Quay lại
        </button>
      </div>
    </modal>
    <div class="title">Sao chép ca theo ngày</div>
    Chọn khoảng thời gian để sao chép ca ngày <b v-if="date">{{date.format('DD-MM-YYYY')}}</b> cho khoảng thời gian từ <b> {{this.copyFrom.format('DD-MM-YYYY')}} </b> tới <b> {{this.copyTo.format('DD-MM-YYYY')}} </b>
    <vue-rangedate-picker
      ref = "rangePicker"
      @selected = "selectedCopyRangeDate"
      class="position-relative calender mt-3 mb-4"
      format="[Ngày] DD [Tháng] MM [Năm] YYYY"
      :presetRanges="{}"
      :captions="{
        'ok_button': 'Xác nhận'
      }"
      :months="['Tháng 1', 'Tháng 2', 'Tháng 3', 'Tháng 4', 'Tháng 5', 'Tháng 6', 'Tháng 7', 'Tháng 8', 'Tháng 9', 'Tháng 10', 'Tháng 11', 'Tháng 12']"
      :shortDays="['CN', 'T2', 'T3', 'T4', 'T5', 'T6', 'T7']">
    </vue-rangedate-picker>
    <div class="d-flex form-group mt-2 flex-wrap">
      <button class="btn btn-danger ml-auto mr-2 col-12 col-md-auto mt-xs-3" @click="copyShift">
        <span class="icon-copy d-inline-block mr-1"></span>
        Sao chép ca theo ngày</button>
      <button class="btn btn-secondary col-12 col-md-auto mt-xs-3" @click="goBack">
        <span class="icon-undo2 d-inline-block mr-1"></span>
        Quay lại</button>
    </div>
    <hr>
    <!-- <div class="title">Sao chép ca theo tuần</div>
    Chọn khoảng thứ trong tuần để tiến hành sao chép từ
    <span v-html="formatTimeRange(this.startDayWeekCopy, this.endDayWeekCopy)"/>
    <vue-rangedate-picker
      ref = "rangePicker"
      @selected = "selectedCopyWeekDate"
      class="position-relative calender mt-3 mb-4"
      format="[Ngày] DD [Tháng] MM [Năm] YYYY"
      :presetRanges="{}"
      :captions="{
        'ok_button': 'Xác nhận'
      }"
      :months="['Tháng 1', 'Tháng 2', 'Tháng 3', 'Tháng 4', 'Tháng 5', 'Tháng 6', 'Tháng 7', 'Tháng 8', 'Tháng 9', 'Tháng 10', 'Tháng 11', 'Tháng 12']"
      :shortDays="['CN', 'T2', 'T3', 'T4', 'T5', 'T6', 'T7']"
    />
    Chọn tuần để sao chép ca <b>thứ được chọn trong tuần hiện tại </b> cho tất cả các thứ <b>trong tuần được chọn</b> <br>
    <p v-if = "this.startWeek">bắt đầu tuần <b> {{this.startWeek.format('dddd [Ngày] DD MMMM YYYY')}} </b> - kết thúc tuần <b> {{this.endWeek.format('dddd [Ngày] DD MMMM YYYY')}} </b> </p>
    <p>Ấn vào ngày thuộc tuần bất kì để thêm tuần đó</p>
    <button class="btn btn-primary ml-auto mr-2" @click="$modal.show('weeekPickerModal')">
        <span class="icon-add d-inline-block mr-1"></span>
        Thêm tuần
    </button>
<table class="table mt-3">
  <thead>
    <tr>
      <th scope="col">Ngày bắt đầu tuần</th>
      <th scope="col">Ngày kết thúc tuần</th>
      <th scope="col">Hành động</th>
    </tr>
  </thead>
  <tbody>
    <tr :key="index" v-for="(week,index) in weeks">
      <th scope="row">{{week.start.format('dddd [Ngày] DD MMMM YYYY')}}</th>
      <th>{{week.end.format('dddd [Ngày] DD MMMM YYYY')}}</th>
      <td>
        <button
        class="btn btn-link py-0"
        @click="deleteWeek(index)">
          <span class="icon-trash d-inline-block mr-1"></span>
          Xóa
        </button>
      </td>
    </tr>
  </tbody>
</table>
<div class="d-flex form-group mt-2 flex-wrap">
      <button class="btn btn-danger ml-auto mr-2 col-12 col-md-auto" @click="copyShiftByWeek">
        <span class="icon-copy d-inline-block mr-1"></span>
        Sao chép ca theo tuần</button>
      <button class="btn btn-secondary col-12 col-md-auto mt-xs-3" @click="goBack">
        <span class="icon-undo2 d-inline-block mr-1"></span>
        Quay lại</button>
    </div> -->
  </div>
</template>

<script>
import {
  vi
} from 'vuejs-datepicker/dist/locale'
import Datepicker from 'vuejs-datepicker'
import HotelDatePicker from 'vue-hotel-datepicker'
import moment from 'moment'
import request from 'superagent'
import appHelper from '../../helper/helper.js'
import {mapState} from 'vuex'
import VueRangedatePicker from 'vue-rangedate-picker'

export default {
  computed: mapState([
    'idToken'
  ]),

  methods: {
    deleteWeek (index) {
      let context = this
      appHelper.datCauHoi('Bạn có muốn xóa tuần này không', () => {
        context.weeks.splice(index, 1)
      }, this)
    },

    selectedCopyRangeDate ({start, end}) {
      if (start == null || end == null) {
        appHelper.taoModalBaoLoi('Ngày bắt đầu và ngày kết thúc không được bỏ trống', this)
        return
      } else if (moment(start).isBefore(moment())) {
        appHelper.taoModalBaoLoi('Không được chọn ngày trong quá khứ', this)
        return
      }
      this.copyFrom = moment(start).subtract(1, 'day')
      this.copyTo = moment(end).subtract(1, 'day')
    },

    selectedCopyWeekDate ({start, end}) {
      let startWeekMoment = moment(start).subtract(1, 'day')
      let endWeekMoment = moment(end).subtract(1, 'day')

      if (start == null || end == null) {
        appHelper.taoModalBaoLoi('Ngày bắt đầu và ngày kết thúc không được bỏ trống', this)
        return
      } else if (this.startWeek.isAfter(startWeekMoment, 'day') || this.endWeek.isBefore(endWeekMoment, 'day')) {
        appHelper.taoModalBaoLoi(
          `Nằm trong khoảng ${this.startWeek.format('DD-MM-YYYY')} tới ${this.endWeek.format('DD-MM-YYYY')}`
          , this
          , 'Khoảng thời gian phải nằm trong tuần được chọn')
        return
      }

      this.startDayWeekCopy = startWeekMoment.toDate()
      this.endDayWeekCopy = endWeekMoment.toDate()
    },

    goBack () {
      this.$router.replace(`/clinic/${this.id}/shift`)
    },

    weekPickerOnSelected (date) {
      // Chuyển thành tuần
      var start = moment(date).startOf('week')
      var end = moment(date).endOf('week')
      let week = {
        start,
        end
      }

      if (moment().isAfter(start)) {
        appHelper.taoModalBaoLoi('Tuần thêm không được nằm trong quá khứ', this)
        return
      }

      // Kiểm tra đã chọn chưa
      for (let i = 0; i < this.weeks.length; i++) {
        if (week.start.isSame(this.weeks[i].start)) {
          appHelper.taoModalBaoLoi(
            `bắt đầu ${week.start.format('dddd [Ngày] DD MMMM YYYY')} kết thúc ${week.end.format('dddd [Ngày] DD MMMM YYYY')}`,
            this,
            'Tuần đã tồn tại')
          return
        }
      }

      // Kiểm tra
      if (this.startWeek.isSameOrBefore(week.start, 'day') && this.endWeek.isSameOrAfter(week.end, 'day')) {
        appHelper.taoModalBaoLoi(
          `tuần sao chép không được trùng tuần bị sao chép`
          , this
          , 'Đã xảy ra lỗi')
        return
      }

      this.weeks.push(week)

      // Kiểm tra là đã chọn
      this.$modal.hide('datePickerModal')
    },

    copyShiftByWeek () {
      if (this.weeks.length === 0) {
        appHelper.taoModalBaoLoi('Chưa có tuần được sao chép nào được thêm', this)
        return
      }

      // Lấy cái ngày được chọn của tuần này
      /**
       * Startweek: ngày bắt đầu tuần bắt đầu sao chép
       * endWeek: ngày kết thúc của tuần bắt đầu sao chép
       * startDayWeekCopy: ngày được chon tuần sao chép
       * endDayWeekCopy ngày kết thúc tuần sao chép
       * startWeekDiff: khác startWeek và startDayWeekCopy
       * endWeekDiff: ngược lại
       */

      let startDayWeekCopyMoment = moment(this.startDayWeekCopy)
      let endDayWeekCopyMoment = moment(this.endDayWeekCopy)
      let startWeekDiffAsDay = Math.floor(Math.abs(moment.duration(startDayWeekCopyMoment.diff(this.startWeek)).asDays()))
      let endWeekDiffAsDay = Math.floor(Math.abs(moment.duration(endDayWeekCopyMoment.diff(this.endWeek)).asDays()))

      // Tạo đối tượng cần copy
      /**
       * Với mỗi tuần ta sẽ có
       * đầu tuần
       * cuối tuần
       * bắt đầu copy đầu tuần + startweekdiff
       * kết thúc copy cuối tuần + endweekdiff
       */
      let checkDatesIsEmptyPromises = []
      var targetWeeks = this.weeks.map(week => {
        let startCopy = moment(week.start).clone().add(startWeekDiffAsDay, 'day')
        let endCopy = moment(week.end).clone().subtract(endWeekDiffAsDay, 'day')

        // Kiểm tra xem đã có ca nào được tạo chưa
        for (var m = moment(startCopy); m.isSameOrBefore(endCopy, 'day'); m.add(1, 'day')) {
          let currentM = m.clone()
          checkDatesIsEmptyPromises.push(new Promise((resolve, reject) => {
            request
              .get(`http://deltavn.net/api/clinic/${this.$route.params.id}/shift/filter`)
              .query({
                date: currentM.format('YYYY-MM-DD')
              })
              .set({
                'Authorization': `Bearer ${this.idToken}`
              })
              .then((res) => {
                if (res.body.length === 0) {
                  resolve()
                } else {
                  let err = `Ngày đã tồn tại ca : ${currentM.format('DD-MM-YYYY')}`
                  reject(err)
                }
              })
          }))
        }

        return {
          startCopy,
          endCopy
        }
      })

      // Kiểm tra xem đã có ca nào được tạo chưa
      let fetchShiftsPromises = []
      for (var m = moment(startDayWeekCopyMoment); m.isSameOrBefore(endDayWeekCopyMoment, 'day'); m.add(1, 'day')) {
        let currentM = m.clone()
        fetchShiftsPromises.push(new Promise((resolve) => {
          request
            .get(`http://deltavn.net/api/clinic/${this.$route.params.id}/shift/filter`)
            .query({
              date: currentM.format('YYYY-MM-DD')
            })
            .set({
              'Authorization': `Bearer ${this.idToken}`
            })
            .then((res) => {
              resolve(res.body)
            })
        }))
      }

      Promise.all(checkDatesIsEmptyPromises).then(() => {
        Promise.all(fetchShiftsPromises).then((shiftsArr) => {
          for (var week of targetWeeks) {
            var i = 0
            for (var m = moment(week.startCopy); m.isSameOrBefore(week.endCopy); m.add(1, 'day')) {
              var currentM = m.clone()
              for (var shift of shiftsArr[i]) {
                let startshift = moment(shift.start_shift).format('HH:mm:[00]')
                let endshift = moment(shift.end_shift).format('HH:mm:[00]')
                let obj = {
                  start_shift: currentM.format('YYYY-MM-DD ') + startshift,
                  end_shift: currentM.format('YYYY-MM-DD ') + endshift
                }
                console.log(obj)
              }
              i++
            }
          }
        })
      }).catch((err) => {
        appHelper.taoModalBaoLoi(`Ngày sao chép ca phải là ngày trống`, this, err)
      })
    },

    copyShift () {
      appHelper.datCauHoi(
        `Bạn có muốn sao chép ca khám bệnh từ ngày ${this.copyFrom.format('DD-MM-YYYY')} tới ngày ${this.copyTo.format('DD-MM-YYYY')}`, () => {
          // Kiểm tra tất cà ca
          let checkIfHaveShiftCreatedPromises = []
          for (let m = moment(this.copyFrom); m.isSameOrBefore(this.copyTo, 'day'); m.add(1, 'days')) {
            let date = m.format('YYYY-MM-DD')
            let currentDate = m.clone()
            let promise = new Promise((resolve, reject) => {
              request.get(`http://deltavn.net/api/clinic/${this.id}/shift/filter?date=${date}`)
                .set({
                  'Authorization': 'Bearer ' + this.idToken
                }).then((res) => {
                  let activeShift = res.body.filter(shift => shift.active === 1)
                  if (activeShift.length) {
                    /* eslint prefer-promise-reject-errors: "off" */
                    reject(`Ngày ${currentDate.format('DD-MM-YYYY')} đã có ca được tạo rồi`)
                  } else {
                    resolve()
                  }
                })
            })

            checkIfHaveShiftCreatedPromises.push(promise)
          }

          let self = this
          Promise.all(checkIfHaveShiftCreatedPromises).then(async (reses) => {
            // Kiểm tra phòng khám đc tạo chưa
            let promises = []
            this.$store.dispatch('set', {
              propertyName: 'isLoading',
              payload: true
            })

            for (let i = 0; i < self.shifts.length; i++) {
              // Lấy giờ bắt đầu, giờ kết thúc
              let startShiftTime = moment(self.shifts[i].start_shift).format('HH:mm:[00]')
              let startEndTime = moment(self.shifts[i].end_shift).format('HH:mm:[00]')
              for (let m = moment(self.copyFrom); m.isSameOrBefore(self.copyTo, 'day'); m.add(1, 'days')) {
                let date = m.format('YYYY-MM-DD')
                let startShiftDateTime = date + ' ' + startShiftTime
                let endShiftDateTime = date + ' ' + startEndTime

                promises.push(
                  request.post(`http://deltavn.net/api/clinic/${this.id}/shift`)
                    .set({
                      'Authorization': `Bearer ${self.idToken}`
                    })
                    .send({
                      start_shift: startShiftDateTime,
                      end_shift: endShiftDateTime
                    })
                )
              }

              // Tạo ca hoàn thành
              Promise.all(promises).then(() => {
                // Thông báo sao chép ca thành công
                appHelper.taoModalThongBao('Sao chép ca thành công', self)
                self.$router.replace(`/clinic/${this.id}/shift`)
              }).finally(() => {
                this.$store.dispatch('set', {
                  propertyName: 'isLoading',
                  payload: false
                })
              })
            }
          }).catch(err => {
            appHelper.taoModalBaoLoi('Để sao chép ca yêu cầu ngày được chọn chưa tạo ca nào', this, err)
          })
        }, this)
    },

    formatTimeRange (start, end) {
      if (!start || !end) return ''
      return `<b>${moment(start).format('dddd [Ngày] DD MMMM YYYY')}</b> tới  <b>${moment(end).format('dddd [Ngày] DD MMMM YYYY')}</b>`
    }
  },

  mounted () {
    this.$refs.rangePicker.dateRange = {
      start: this.date.clone().add(1, 'day').toDate(),
      end: this.date.clone().add(1, 'day').toDate()
    }

    this.copyRangeFromStart = this.date.clone()
    this.copyRangeFromEnd = this.date.clone()

    this.ptBr = {
      night: 'ngày',
      nights: 'ngày',
      'day-names': ['CN', 'Th2', 'Th3', 'Th4', 'Th5', 'Th6', 'Th7'],
      'check-in': 'Bắt đầu',
      'check-out': 'kết thúc',
      'month-names': ['Tháng 1', 'Tháng 2', 'Tháng 3', 'Tháng 4', 'Tháng 5', 'Tháng 6', 'Tháng 7', 'Tháng 8', 'Tháng 9', 'Tháng 10', 'Tháng 11', 'Tháng 12']
    }
  },

  data () {
    return {
      rangePickerError: '',
      isPickWeek: true,
      startDayWeekCopy: moment(Number(this.$route.params.date)).toDate(),
      endDayWeekCopy: moment(Number(this.$route.params.date)).toDate(),
      vi: vi,
      val: null,
      copyFrom: moment(this.tomorrow),
      copyTo: moment(this.tomorrow),
      smallestStartDay: moment('01-01-1990', 'DD-MM-YYYY').toDate(),
      disableDatePickerDates: {
        // Chỉ được chọn tuần sau trở đi
      },
      disableRangePickerDates: {
        to: moment(Date()).subtract(1, 'day').toDate()
      },
      ptBr: {
        night: 'ngày',
        nights: 'ngày',
        'day-names': ['CN', 'Th2', 'Th3', 'Th4', 'Th5', 'Th6', 'Th7'],
        'check-in': 'Bắt đầu',
        'check-out': 'kết thúc',
        'month-names': ['Tháng 1', 'Tháng 2', 'Tháng 3', 'Tháng 4', 'Tháng 5', 'Tháng 6', 'Tháng 7', 'Tháng 8', 'Tháng 9', 'Tháng 10', 'Tháng 11', 'Tháng 12']
      },
      weeks: [],
      copyRangeFromStart: null,
      copyRangeFromEnd: null,
      CopyRanges: [],
      date: moment((Number(this.$route.params.date))),
      startWeek: moment(Number(this.$route.params.date)).startOf('week'),
      endWeek: moment(Number(this.$route.params.date)).endOf('week')
    }
  },

  async created () {
    this.CopyRange = 0

    // Lưu lại dữ liệu để kiếm tra xem thêm tuần có bị trùng không
    this.startDayWeek = moment(Number(this.$route.params.date))
    this.endDayWeek = moment(Number(this.$route.params.date))

    // Lấy ngày mai
    let today = new Date()
    today.setDate(today.getDate() + 1)
    this.tomorrow = today
    this.copyFrom = moment(today)
    this.copyTo = moment(today)
    // lấy id
    this.id = this.$route.params.id

    // Lấy ca của phòng khám
    let date = moment((new Date(Number(this.$route.params.date))))
    this.shifts = await request.get(`http://deltavn.net/api/clinic/${this.id}/shift/filter?date=${date.format('YYYY-MM-DD')}`).set({
      'Authorization': `Bearer ${this.idToken}`
    })
    this.shifts = this.shifts.body

    // Tạo disable date quá khứ
    this.nextWeek = moment()
    this.nextWeek.add(1, 'weeks').startOf('isoWeek')
  },

  components: {
    HotelDatePicker,
    Datepicker,
    VueRangedatePicker
  }
}
</script>

<style>
.v--modal-overlay .v--modal-box {
  height: auto !important;
}

.datepicker__clear-button {
  display: none;
}

.position-relative {
  position: relative;
}

.input-date {
  width: 100% !important;
  padding: 12px !important;
  border-radius: 2px !important;
  font-size: 14px !important;
  color: #35343d !important;
  font-family: Muli, sans-serif !important;
}

.calendar-head h2 {
  display: none
}

.calendar {
  width: 80% !important;
  height: auto !important;
}

.calendar-wrap {
  width: 100% !important;
  background: white
}

@media (max-width: 720px) {
  .calendar-wrap {
    width: 110vw !important;
  }
}

.calendar_weeks li {
  font-size: 12px !important;
  font-weight: 400 !important;
  color: #999 !important;
  text-align: center !important;
  font-family: Muli, sans-serif !important;
}

.months-text,
.calendar_days li {
  padding: 6px !important;
  font-size: 1.2em;
  font-family: Muli, sans-serif !important;
}

.calendar_days li:hover {
    background: #477bd4 !important;
    color: white !important
}

.datepicker__month-day--first-day-selected, .datepicker__month-day--last-day-selected {
    background: #5089ea !important;
    color: #fff;
}

.datepicker__month-day--selected {
    background-color: rgb(119, 170, 255) !important;
    color: #fff;
}

.datepicker__dummy-wrapper--is-active {
    border: 1px solid #6c757d66 !important;
}

.datepicker__month-day--selected:hover {
    color: white !important;
}

li.calendar_days_in-range {
  background:  #5089ea !important
}

.datepicker {
  box-shadow: 0px 1px 5px 1px #00000052;
}

.calendar-wrap {
  box-shadow: 3px 1px 1px #00000026 !important;
}

.calendar-btn-apply {
    background: #4f89ea !important;
    margin-top: 1em;
    position: absolute !important;
    bottom: 25px !important;
    left: 25px !important;
    width: 20% !important;
}

@media (max-width: 420px) {
  .v--modal-box {
    width: auto !important
  }
}

.v--modal-overlay {
  overflow: scroll;
}
</style>



