<template>
  <div class="">
      <div class="title mb-2">Đăng ký</div>
      <div class="form-group">
        <label>Họ tên</label>
        <input
        type="text"
        :class="['form-control', this.errors.first('name') ? 'is-invalid' : '']"
        placeholder="Họ tên"
        v-model = 'name'
        data-vv-as="Họ tên"
        name = 'name'
        v-validate="'required'">
        <div class="text-danger pt-2">{{ this.errors.first('name') }}</div>
      </div>
      <div class="form-group">
        <label>Tên đăng nhập</label>
        <input
        type="text"
        :class="['form-control', this.errors.first('username') ? 'is-invalid' : '']"
        placeholder="Tên đăng nhập"
        v-model = 'userName'
        data-vv-as="Tên đăng nhập"
        name = 'username'
        v-validate="'required'">
        <div class="text-danger pt-2">{{ this.errors.first('username') }}</div>
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Mật khẩu</label>
        <input
        type="password"
        :class="['form-control', this.errors.first('password') ? 'is-invalid' : '']"
        placeholder="Mật khẩu"
        v-model = 'passWord'
        data-vv-as="Mật khẩu"
        name="password"
        v-validate="'required|min:8'">
        <div class="text-danger pt-2">{{ this.errors.first('password') }}</div>
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Nhập lại Mật khẩu</label>
        <input
        type="password"
        :class="['form-control', this.errors.first('retypePassword') ? 'is-invalid' : '']"
        placeholder="Nhập lại mật khẩu"
        v-model = 'retypePassword'
        data-vv-as="Nhập lại mật khẩu"
        name = 'retypePassword'
        v-validate="'required|min:8'">
        <div class="text-danger pt-2">{{ this.errors.first('retypePassword') }}</div>
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Email</label>
        <input
        type="text"
        :class="['form-control', this.errors.first('email') ? 'is-invalid' : '']"
        placeholder="Email"
        v-model = 'email'
        data-vv-as="Email"
        name = 'email'
        v-validate="'required|email'">
        <div class="text-danger pt-2">{{ this.errors.first('email') }}</div>
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Địa chỉ</label>
        <input
        type="text"
        :class="['form-control', this.errors.first('address') ? 'is-invalid' : '']"
        placeholder="Địa chỉ"
        v-model = 'address'
        data-vv-as="Địa chỉ"
        name = 'address'
        v-validate="'required'">
        <div class="text-danger pt-2">{{ this.errors.first('address') }}</div>
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Số điện thoại</label>
        <input
        type="text"
        :class="['form-control', this.errors.first('phone') ? 'is-invalid' : '']"
        placeholder="Số điện thoại"
        v-model = 'phone'
        data-vv-as="Số điện thoại"
        name = 'phone'
        v-validate="'required|numeric|min:10'">
        <div class="text-danger pt-2">{{ this.errors.first('phone') }}</div>
      </div>
      <button type="submit" class="btn btn-primary" @click = "register">
        <span class="icon-sign-up d-inline mr-2"></span>
        Đăng ký
      </button>
  </div>
</template>

<script>
import request from 'superagent'
import appHelper from '../helper/helper.js'
export default {
  data () {
    return {
      name: '',
      userName: '',
      passWord: '',
      email: '',
      address: '',
      phone: '',
      retypePassword: ''
    }
  },

  methods: {
    verifyRecaptcha (response) {
      this.verify = response
    },

    register () {
      let self = this
      this.$validator.validate().then(result => {
        if (result) {
          if (this.retypePassword !== this.passWord) {
            self.errors.add('password', 'Mật khẩu không bằng mật khẩu nhập lại mật khẩu')
            return
          }

          let obj = {
            name: this.name,
            username: this.userName,
            password: this.passWord,
            email: this.email,
            address: this.address,
            phone: this.phone
          }

          request.post('http://deltavn.net/api/user')
            .send(obj).then((res) => {
              appHelper.taoModalThongBao('Tạo tài khoản thành công')
              this.$store.dispatch('toggle', 'isShowModal')
            }).catch((err) => {
              // Thêm các lỗi server side vào vue
              let errors = err.response.body.messages
              for (let key in errors) {
                self.errors.add(key, errors[key].toString())
              }
            })
        }
      })
    }
  }
}
</script>

<style>

</style>



