<template>
  <div id="app">
    <transition name = 'fade'>
      <div  id = "modalTopContainer" class="d-flex align-items-center justify-content-center" v-if="isShowModal">
          <div id = "modalContainer" :class="isShowSideBar ? 'enable-side-bar' : 'disable-side-bar'">
            <button class="btn" @click="$store.dispatch('toggle', 'isShowModal')">
              <span class="icon-close"></span>
            </button>

            <router-view v-show="!isLoading"
              id = "content" />
            <div  v-show="isLoading" id = "content">
              <div class="font-weight-bold">
                Đang tải dữ liệu
              </div>
            </div>
          </div>
      </div>
    </transition>
    <Sidebar />
    <v-dialog/>
    <home></home>

    <!-- Phòng khám -->
    <div class="clinicSearch" v-if="clinics != null && isShowModal === false && isShowSideBar === false">
      <button class="btn btn-danger mr-2" @click="stopClinicSearch">
        <span class="icon-close d-inline mr-2"></span>
        Không tìm nữa
      </button>
      <button class="btn btn-primary mt-xs-3" @click="backClinicSearch">
        <span class="icon-undo2 d-inline mr-2"></span>
        Quay lại tìm kiếm phòng khám
      </button>
    </div>
  </div>
</template>

<script>
/** Bootstrap */
import './assets/css/bootstrap.min.css'

/** Icon */
import './assets/css/icon.css'

/** Sidebar */
import Sidebar from './components/Sidebar'
import request from 'superagent'

/** Store */
import { mapState } from 'vuex'
import store from './store/index.js'
import Home from './components/Home'

export default {
  name: 'App',
  computed: mapState([
    'isShowSideBar',
    'isShowModal',
    'isLoading',
    'clinics'
  ]),
  components: {
    Sidebar,
    Home
  },
  destroyed () {
    this.websocket.close()
  },
  methods: {
    stopClinicSearch () {
      store.dispatch('set', {
        propertyName: 'clinics',
        payload: null
      })
    },

    backClinicSearch () {
      store.dispatch('set', {
        propertyName: 'clinics',
        payload: null
      })

      this.$router.replace('/clinic/find')
      store.dispatch('set', {
        propertyName: 'isShowModal',
        payload: true
      })
    }
  },

  created () {
    let idToken = localStorage.getItem('idToken')
    if (idToken) {
      store.dispatch('set', {
        propertyName: 'idToken',
        payload: idToken
      })
      // Lấy thông tin cá nhân (id)
      request.post('http://deltavn.net/api/me').set({
        'Authorization': `Bearer ${idToken}`
      }).then((res) => {
        store.dispatch('set', {
          propertyName: 'user',
          payload: res.body.data
        })
      })
    }

    /**
     * Init web socket
     * Start fetch data from realtime
     */
    var reconnect = null
    this.websocket = new WebSocket('ws://192.168.1.25:8000')
    this.websocket.onclose = ({data}) => {
      reconnect = setInterval(() => {
        console.log('attemp to reconnect')
        this.websocket = new WebSocket('ws://192.168.1.25:8000')
        this.websocket.onopen = () => {
          if (reconnect != null) {
            clearInterval(reconnect)
            reconnect = null
          }
        }
      }, 5000)
    }
    this.websocket.onopen = () => {
      this.websocket.onmessage = ({data}) => {
        let parseData = JSON.parse(data)
        switch (parseData.type) {
          case 'add':
          case 'initial':
            store.dispatch('addReport', parseData.new_val)
            break
          case 'remove':
            store.dispatch('deleteReport', parseData.old_val)
            break
          case 'change':
            store.dispatch('editReport', parseData.new_val)
            break
        }
      }
    }
  },
  store
}
</script>

<style>
.vue-dialog-button:focus,
.vue-dialog-button:active {
  outline: 0
}

.vue-dialog-button:hover {
    background: rgba(0, 0, 0, 0.15) !important;
    color: #ff403c !important
}

.vue-dialog .dialog-content {
  font-size: 18px;
}

.vue-dialog-button {
    font-size: 18px !important;
}

.title {
  font-weight: bold;
  font-size: 1.2em
}

.enable-side-bar {
  margin-left: 300px
}

.fade-enter-active, .fade-leave-active {
  transition-property: opacity;
  transition-duration: .15s;
}

.fade-enter-active {
  transition-delay: .15s;
}

.fade-enter, .fade-leave-active {
  opacity: 0
}

#modalTopContainer {
  background-color: #00000042;
  position: absolute;
  width: 100%;
  height: 100%;
}

#modalContainer {
  position: relative;
  transition: all 0.3s ease-in-out;
  min-width: 30%;
  max-width: 100%;
}

#modalContainer > button {
  position: absolute;
  top: 15px;
  right: 15px;
}

#content {
  padding: 30px;
  background: white;
  box-shadow: 1px 1px 10px 2px #00000042;
  overflow: auto;
  max-height: 100vh;
}

@media (max-width: 420px) {
  height: 100vh;
}

body {
  font-family: sans-serif;
  font-size: 1em;
  line-height: 1.5;
  color: #333;
}

[class^="icon-"], [class*=" icon-"] {

  font-size: 1.2em;
  display: block;
}

.alert-danger {
  color: #c50517
}

body {
  font-family: 'Muli', sans-serif !important;
}

@media (max-width: 600px) {
  .enable-side-bar {
    margin-left: 700px
  }
}

.w-500 {
  width: 1000px
}

.loading {
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.2);
}

@media (min-width: 720px) {
  .w-60 {
   width: 60vw !important
  }
}

@media (min-width: 420px) and (max-width: 720px) {
  .w-60 {
   width: 80vw !important
  }
}

@media (max-width: 420px) {
  .w-60 {
   width: 100vw !important
  }
}

@media (max-width: 769px) {
  .mt-xs-3 {
    margin-top: 0.7em
  }
}

.clinicSearch {
    position: absolute;
    bottom: 20px;
    right: 20px;
}

.vdp-datepicker__calendar .cell.selected:hover {
  background: #478bfb;
}

.vdp-datepicker__calendar .cell.selected {
  background: #2b6ee0;
  color: white;
}

.vdp-datepicker__calendar .cell.day-header {
    font-size: 1em;
}

.vdp-datepicker__calendar .cell:not(.blank):not(.disabled).day:hover {
  border: 1px solid #2b6ee0;
}

.btn-primary-special {
    color: #fff;
    background-color: #007bff8a;
    border-color: #5091d8;
}
</style>



