<template>
    <div>
        <div class='title mb-3'>Ca khám bệnh đã đăng ký</div>
        <table class="table">
          <thead>
            <tr>
              <th>Phòng khám</th>
              <th>Bắt đầu ca</th>
              <th>Kết thúc ca</th>
              <th>Trạng thái</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="shift in shifts" :key="shift.id">
              <td>
                <span v-if="shift.clinic">
                  {{shift.clinic.name}}
                </span>
              </td>
              <td>{{shift.start_shift}}</td>
              <td>{{shift.end_shift}}</td>
              <td>{{shift.pivot.confirmed ? 'Đã được duyệt' : 'Chưa được duyệt'}}</td>
            </tr>
          </tbody>
      </table>
    </div>
</template>

<script>
import request from 'superagent'
import {mapState} from 'vuex'
import Vue from 'vue'

export default {
  computed: mapState([
    'idToken',
    'user'
  ]),

  data () {
    return {
      shifts: []
    }
  },

  created () {
    this.$store.dispatch('set', {
      propertyName: 'isLoading',
      payload: true
    })

    request.get(`http://deltavn.net/api/user/${this.user.id}/book`).set({
      'Authorization': `Bearer ${this.idToken}`
    }).then((res) => {
      this.shifts = res.body
      this.$store.dispatch('set', {
        propertyName: 'isLoading',
        payload: false
      })

      // Lấy thông tin phòng khám

      this.shifts.forEach(shift => {
        request.get('http://deltavn.net/api/clinic/' + shift.clinic_id).then((res) => {
          Vue.set(shift, 'clinic', res.body.data)
        })
      })
    })
  }
};
</script>

<style>
</style>



